<template>
  <div class="container">
    <Header @toggle-add-task="toggleAddTask" title="Task tracker:"
    :showButton="showAddTask" />
    <div v-if="showAddTask">
      <AddTask @add-task="addTask" />
    </div>
    <Tasks @toggle-reminder="toggleReminder" @delete-task='deleteTask' :tasks="tasks" />
  </div>
</template>

<script>
import Header from './components/Header'
import Tasks from './components/Tasks'
import AddTask from './components/AddTask'

export default {
  name: 'App',
  components: {
    Header,
    Tasks,
    AddTask
  },
  data() {
    return {
      tasks: [],
      showAddTask: false

    }
  },
  methods: {
    toggleAddTask() {
      this.showAddTask = !this.showAddTask
    },
    addTask(task) {
      this.tasks = [...this.tasks, task]
    },
    deleteTask(id) {
      if(confirm('Are you sure?')){
        this.tasks = this.tasks.filter((task) => task.id !== id)
      }
    },
    toggleReminder(id) {
      // forEach task if the task.id is equal to the id is passed
      // In return it shows an array of objects where we want to change
      // the reminder to the opposite value
      this.tasks = this.tasks.map((task) => task.id === id ? {
        ...task, reminder: !task.reminder } : task // if it doesn't math we don't return anything
      )
    },
  },
  created(){
    this.tasks = [
      {
        id: 1,
        text: 'Dentist Appointment',
        day: '22/04/2021',
        reminder: true
      },
      {
        id: 2,
        text: 'Doctor Appointment',
        day: '05/05/2021',
        reminder: false
      }
    ]
  }
}
</script>

